@import "../../styles/sass/variable";

.title {
    margin: 0;
    position: relative;
    & h2{
        margin: 0 0 30px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        color: $textColor;
        text-transform: capitalize;
        font-size: 36px;
        line-height:1.2;
        letter-spacing: 5px;
        position: relative;
    }
    &::before {
        content: "";
        position: absolute;
        background: $primaryColor;
        width: 55px;
        height: 1px;
        bottom:-10px;
        left:50%;
        transform: translate(-50%,50%);
        margin-bottom: 30px;
    }
}







